	module.exports = {"data":{"markdownRemark":{"html":"<h2>Social Engineering</h2>\n<p>Social Engineering is the art of gaining access to a secured system or resource by exploiting human behavior. It involves tricking people into breaking normal security procedures. Most attack vectors rely heavily on leveraging technical skills to find gaps in the security system. Social Engineering relies heavily on having a good understanding of human psychology. Thoroughly researching the target before an attack makes social engineering a powerful tool in the hands of the attacker. </p>\n<h4>Traits of a good Social Engineering Hacker</h4>\n<ul>\n<li>Demonstrates high emotional intelligence</li>\n<li>Intuitive understanding of human psychology</li>\n<li>Charming and persuasive</li>\n<li>Patient and observant</li>\n<li>Adept at predicting human behavior based on exploiting the human need to be helpful, curious, greedy and vain</li>\n</ul>\n<h4>Some examples of Social Engineering hacks</h4>\n<ul>\n<li>\n<p>Baiting: Leaving a malware infected USB at a coffee shop in the hope that someone is curious enough to plug it in and check it out. Once the person plugs the USB in, malware is installed on their computer.</p>\n</li>\n<li>\n<p>Pretexting: Telling lies to gain access to private information. An example would be impersonating a bank officer and asking people for personal information to ‘confirm their account’.</p>\n</li>\n<li>\n<p>Phishing: Sending an email which looks like it is from a trusted source to bait the user into clicking a link (to install malware) or replying with private information.</p>\n</li>\n<li>\n<p>Infiltrating: impersonating someone legitimate in order to gain physical access to a building/office etc., e.g. the coffee machine repair person</p>\n</li>\n</ul>\n<h4>More Information:</h4>\n<p>Read on more information on social engineering hacks and steps you can take to protect yourself from one:\n<a href='https://www.webroot.com/us/en/home/resources/tips/online-shopping-banking/secure-what-is-social-engineering' target='_blank' rel='nofollow'>What is Social Engineering?</a></p>\n<p><a href='http://www.makeuseof.com/tag/protect-8-social-engineering-attacks/' target='_blank' rel='nofollow'>Protect Yourself from Social Engineering Attacks</a></p>\n<p><a href='https://www.darkreading.com/the-7-best-social-engineering-attacks-ever/d/d-id/1319411?' target='_blank' rel='nofollow'>7 Best Social Engineering Hacks Ever</a></p>","fields":{"slug":"/security/social-engineering/"},"frontmatter":{"title":"Social Engineering"}}},"pathContext":{"slug":"/security/social-engineering/"}}

